* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list{
  list-style: none;
  margin: 0;
  padding: 0;
}
.countries-item {
  display: flex;
    padding: 10px;
    gap: 10px;
    align-items: center;
}
.country-title {
  display: flex;
  gap: 10px;
  align-items: center;
}
.counries-item_flag,
.countries-item_name {
  margin: 0;
    padding: 0;
}